import { Component, ViewChild } from '@angular/core';
import { AppService } from './app.service';

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.css']
})
export class AppComponent {
	rangdeArenaGames : any[] = [];
	rangdeArenaGameTitles : string[] = [];
	api_rate_limit : any;
	sortKey: string = 'title';
	reverse: boolean = false;
    filteredRangDeGame : any;
    itemsPerPage : number = 10;
    currentPage : number = 1;

    constructor(
        private _appService: AppService
        ) {
    }

    ngOnInit(){
        this.getGamesArena()
    }

    sortGames(key){
        this.sortKey = key;
        this.reverse = !this.reverse;
    }

    getGamesArena(){
        this._appService.getGamesArena().subscribe(
            response => {
                this.api_rate_limit = response[0].api_rate_limit;
                this.rangdeArenaGames = response.filter(game => game.title);
                this.rangdeArenaGameTitles = this.rangdeArenaGames.map(game => game.title);
            },
            error => {
                console.log(error);
            }
            );
    }
}



